.wrapper{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.loginPasswordtError{
	color: red;
	width: 200px;
	text-align: center;
}
.loginFormError{
	border: solid rgba(255, 0, 0, 0.507) 1px;
}
.loginFormTextError{
	margin: 0 0 0 10px;;
	padding: 0 10px;
	color: red;
	border: solid rgba(255, 0, 0, 0.507) 1px;
}
.loginInput{
	padding: 10px 0;
}
.loginFormInput{
	width: 200px;
}


.loginCheckbox{
	margin: 3px 10px 3px 1px;
}
.loginButton{
	display: flex;
	justify-content: end;
	margin: 10px 0 0 0;
}
.loginButtonInaccessible,
.captchaSymbols{
	border: darkgray 1px solid;
	color: darkgrey;
}

.captcha,
.captchaInput{
	margin: 10px 0;
}

.captchaSymbols{
	max-width: 100%;
}